import React from 'react';
import $ from 'jquery';
import "./Replay.css";

class Replay extends React.Component{
	constructor(props){
		super(props);
		this.state = {
			play: false,
			finish: false
		};
		this.timer = null;
		this.count = 0;		// count is odd, player X goes; count is even (>0), player O goes
	}

	/*-----	Play/Pause the demo -----*/
	Play(){
		if(this.state.play===false){
			/*	Means currently paused, need continue to play */
			this.setState((prevState, props)=>({
				play: !prevState.play
			}),()=>{
				this.timer = setInterval(()=>this.Show(), 1000)
			});
		}
		else{
			/*	Means currently playing, need pause */
			this.setState((prevState, props)=>({
				play: !prevState.play
			}),()=>{
				clearInterval(this.timer);
			});
		}
	}
	
	/* -------      This function will run every 1000 ms once playing until pause or finished --------- */ 
	Show(){
		var nextAction = this.props.history[this.count];
		if(nextAction === undefined ){
			this.setState((prevState, props)=>({
				play: !prevState.play,
				finish: true
			}),()=>{
				clearInterval(this.timer);
				this.count = 0;
				$("#ReplayButton").attr("disabled", true);
			});
		}
		else{
			this.count ++;
			this.placeChessman(nextAction, this.count);
		}
	}
	
	placeChessman(action, count){
		var chessman = count%2===1?"X":"O";
		var x = action[0];
		var y = action[1];
		var row = $("#replayboard").find("tr").eq(y);
		var cell = $(row).children("td").eq(x);
		$(cell).text(chessman);
	}
	
	Reset(){
		var element = $("#replayboard").find("td");
		$(element).text("");
		$("#ReplayButton").removeAttr("disabled");
		this.count = 0;
		this.setState(()=>({
			finish: false
		}));
	}
	
	render(){
		var array =[];
		for(var i=0;i<this.props.number;i++)
			array.push(i+1);
		var row = array.map((element, index) => {
			return <td key={index} onClick={this.props.nextstep}></td>
		});
		var rows = array.map(function(element, index){
			return <tr key={index}>{row}</tr>
		});
		var replayTable = <table id="replayboard" className="table table-bordered">
										<tbody>
											{rows}
										</tbody>
									</table>
		var buttonContent = this.state.play===false?"Play":"Stop";
		var finish = this.state.finish===true?"Game replay is finished...":"";
		var classname = this.state.play===false?"btn-outline-success":"btn-outline-primary";
		return	<div className="modal fade" id="ReplayBox" tabIndex="-1">
 						<div className="modal-dialog modal-lg">
    						<div className="modal-content">
      							<div className="modal-header">
        							<h3 className="w-100 text-center mb-0">Game Replay</h3>
      							</div>
      							<div className="modal-body">
									<div className="w-100 d-flex justify-content-center">
										{replayTable}
									</div>
									<p className="my-2 text-center text-success">{finish}</p>
								</div>
								<div className="modal-footer d-flex justify-content-center">	
        							<button className={"btn mt-2 btn-block " + classname} onClick={()=>this.Play()} id="ReplayButton">{buttonContent}</button>
					  				<button className="btn btn-outline-danger mt-2 btn-block" data-dismiss="modal" onClick={()=>this.Reset()}>Close</button>
								</div>
    						</div>
  						</div>
					</div>
	}
}

export default Replay;


