import React from 'react';
import "./ActionPanel.css";

function ActionPanel(props){
	var array = props.list;
	var elements = array.map(function(element, index){
		return <button id={element} key={index} onClick={props.onClick} className="list-group-item text-center">{element}</button>
	});
	return	<div className="list-group">
					{elements}
				</div>
}

export default ActionPanel;


