import React from 'react';
import ReactDOM from 'react-dom';
import "./Board.css";
import $ from 'jquery';



class Chessboard extends React.Component{
	componentDidMount() {
		/*
			Here we do a little thing related to CSS style:   when component loaded, we make table unit as square
		*/
		var element = $(ReactDOM.findDOMNode(this)).find("td");
		var width = element.width();
		element.height(width);
 	}
	render(){
		var array =[];
		for(var i=0;i<this.props.number;i++)
			array.push(i+1);
		var row = array.map((element, index) => {
			return <td key={index} onClick={this.props.nextstep}></td>
		});
		var rows = array.map(function(element, index){
			return <tr key={index}>{row}</tr>
		});
		return	<table id="chessboard" className="w-100 table table-bordered">
						<tbody>
							{rows}
						</tbody>
					</table>
	}
}

export default Chessboard;


