import React from 'react';
import "./Message.css";


function Message(props){
	var type = props.messagetype;
	var winner = props.winner;
	var header = null;
	var content = null;
	var button = <button className="btn btn-outline-success px-5" data-dismiss="modal">OK</button>;
	if(type==="chessmanerror"){
		header = "Invalid Operation";
		content = <p className="text-danger">You cannot place your chessman on the cell which is already used</p>;
	}
	else if(type==="won"){	
		header = "Player "+ winner + " has already won!";
		content = <div>
							<p className="text-danger">Current game is over!</p>
							<p className="text-danger">[1] You can click the restart button to start a new game</p>
							<p className="text-danger">[2] You can click the replay button to review this game</p>
							<p className="text-danger">[3] You can click the undo button to take back the last action</p>
						</div>
	}
	else if(type==="winner appears"){
		header = "Congratulation! Player "+ winner+" !";
		content = <p className="text-danger">You have already won the game!</p>
	}
	else if(type==="game rule"){
		header = "Game Introduction";
		content = <div>
							<p className="text-danger">[1] Each of two players places one chessman in turn</p>
							<p className="text-danger">[2] The first player meet the following condition will win</p>
							<p className="text-danger">[3] Having five consecutive chessmans</p>
							<p className="text-danger">[4] Consecutive lines can be either horizontal, vertical or diagnoal</p>
							<p className="text-danger">[5] Blank or opponent's chessman will interrupt the consecution</p>
						</div>
	}
	else if(type==="undo"){
		header = "Undo Confirmation";
		content = <div>
							<p className="text-danger">Do you really want to take back the last action?</p>
							<p className="text-danger">Once reset, all information about last action will be lost</p>
						</div>
		button = <div className="w-100">
						<button className="btn btn-outline-success mt-2 btn-block" data-dismiss="modal" onClick={props.undoComfirm}>Confirm</button>
					  	<button className="btn btn-outline-danger mt-2 btn-block" data-dismiss="modal">Cancel</button>
					  </div>
	}
	else if(type==="reset confirm"){
		header = "Reset Confirmation";
		content = <div>
							<p className="text-danger">Do you really want to reset the game?</p>
							<p className="text-danger">Once reset, all information of previous game will be lost</p>
						</div>;
		button = <div className="w-100">
						<button className="btn btn-outline-success mt-2 btn-block" data-dismiss="modal" onClick={props.resetComfirm}>Confirm</button>
					  	<button className="btn btn-outline-danger mt-2 btn-block" data-dismiss="modal">Cancel</button>
					  </div>
	}
	return <div className="modal fade" id="MessageBox" tabIndex="-1">
 				<div className="modal-dialog modal-lg">
    				<div className="modal-content">
      					<div className="modal-header">
        					<h3 className="w-100">{header}</h3>
      					</div>
      					<div className="modal-body">
        					{content}
      					</div>
      					<div className="modal-footer d-flex justify-content-center">
        					{button}
      					</div>
    				</div>
  				</div>
			</div>
}

export default Message;


