/*
	We use BootStrap as CSS framework:
	(1) npm install jquery
	(2) npm install popper.js
	(3) npm install bootstrap
	
	For using BootStrap4 in NPM + Webpack, we can refer to here:
	https://code.luasoftware.com/tutorials/bootstrap/setup-bootstrap4-with-npm-and-webpack/
*/


import React from 'react';
import ReactDOM from 'react-dom';
import $ from 'jquery';
import 'bootstrap';												// import 'bootstrap/dist/js/bootstrap'
import 'bootstrap/dist/css/bootstrap.css';		   // import 'bootstrap/scss/bootstrap.scss'
import './index.css';

import Chessboard from './Chessboard/Board.js';
import ActionPanel from './ActionPanel/ActionPanel.js';
import Message from './Modal/Message.js';
import Replay from './Modal/Replay.js';


function squareArray (number){
	var array = new Array(number)
	for(var i=0;i<number;i++){
		array[i] = new Array(number);
		for(var j=0; j<number; j++)
			array[i][j] = null
	}	
	return array;
}


class App extends React.Component{
	constructor(props){
		super(props);
		this.state = {
			message: null,
			currentplayer: false, 
			chessarray: squareArray(this.props.number), 
			history: [],
			winner: null
		}
	}

	/*-----------------------------------	nextstep function is for chessboard -------------------*/
	nextstep(event){
		var element = $(event.target);
		var oldcontent = element.text();
		var content = null;
		if (this.state.winner!=null){
			this.setState({
				message: "won"
			},function(){
				$("#MessageBox").modal({
					backdrop: "static"
				});
			});
		}
		else if (oldcontent.length!==0){
			this.setState({
				message: "chessmanerror"
			},function(){
				$("#MessageBox").modal({
					backdrop: "static"
				});
			});	
		}
		else{
			this.setState(function(prevState, props){
				/*------------       change player    ----------------*/
				var newcurrentplayer = !prevState.currentplayer
				/*------------       update current chessboard    ----------------*/
				var x= element.prevAll().length;
				var y= element.parent().prevAll().length;
				content = this.state.currentplayer ===true?"O":"X";
				var newchessArray = prevState.chessarray.map(function(array) {
    				return array.slice();																		//copy
				});			
				newchessArray[y][x] = content;
				/*------------       save new action in game history    ----------------*/
				var oldhistory = prevState.history;
				var newhistory = [];
				if (oldhistory.length === 0)
					newhistory.push([x, y])
				else{
					newhistory = oldhistory.map(function(array) {
    					return array.slice();																		
					});		
					newhistory.push([x, y]);
				}
				/*------------       place current chessman    ----------------*/
				element.text(content);
				return{
					currentplayer: newcurrentplayer, 
					chessarray: newchessArray, 
					history: newhistory
				}
			}, this.checkWin);
		}
	}
	
	/*-----------------------------------	after every step should check if winner occurs -------------------*/
	checkWin(){
		var currentChessman = this.state.currentplayer === true?"X":"O";			
		//note here this.state.currentplayer refers to the next plater
		var pos = this.state.history[this.state.history.length-1];
		var result = this.pathCompute(currentChessman, pos, this.state.chessarray);
		if(result){		
			this.setState({
				message: "winner appears",
				winner: currentChessman
			},function(){
				$("#MessageBox").modal({
					backdrop: "static"
				});
				/* Once winner appears, this game is over, we are not allowed to use 'Pass' button*/
				$("#Pass").attr("disabled", true);
			});
		}
	}
	
	/*-----------------------------------	compute current consecutive path -------------------*/
	pathCompute(value, pos, array){
		var x = pos[0], y = pos[1];
		var count1, count2, i, j;
		/*-----------------	Compute horizontal line ----------------*/
		count1 = 0;
		count2 = 0;
		for(i=x-1; i>=0; i--){
			if(array[y][i]===value)
				count1++
			else
				break
		}
		for(i=x+1; i<array[y].length; i++){
			if(array[y][i]===value)
				count2++
			else
				break
		}
		if(count1+count2>=4)	return true
		
		/*-----------------	Compute vertical line ----------------*/
		count1 = 0;
		count2 = 0;
		for(i=y-1; i>=0; i--){
			if(array[i][x]===value)
				count1++
			else
				break
		}
		for(i=y+1; i<array.length; i++){
			if(array[i][x]===value)
				count2++
			else
				break
		}
		if(count1+count2>=4)	return true
		
		/*-----------------	Left-top to Right-bottom line ----------------*/
		count1 = 0;
		count2 = 0;
		for(i=x-1, j=y-1; i>=0&&j>=0; i--, j--){
			if(array[j][i]===value)
				count1++
			else
				break
		}
		
		for(i=x+1, j=y+1; i<array.length&&j<array.length; i++, j++){
			if(array[j][i]===value)
				count2++
			else
				break
		}
		if(count1+count2>=4)	return true
		
		/*-----------------	Left-bottom to Right-top line ----------------*/
		count1 = 0;
		count2 = 0;
		for(i=x-1, j=y+1; i>=0&&j<array.length; i--, j++){
			if(array[j][i]===value)
				count1++
			else
				break
		}
		
		for(i=x+1, j=y-1; i<array.length&&j>=0; i++, j--){
			if(array[j][i]===value)
				count2++
			else
				break
		}
		if(count1+count2>=4)	return true
		
		/*----------- else return false ----------*/
		return false
	}
	
	/*-----------------------------------	functionalities for Action Panel (chessboard and states) -------------------*/
	
	onClick(event){
		var type = event.target.id;
		if(type==="Game Rule")
			this.Rule()
		if(type==="Pass")
			this.Pass()
		if(type==="Undo")
			this.Undo()
		if(type==="Info")
			this.Info()
		if(type==="Replay")
			this.Replay()
		if(type==="Reset")
			this.Reset()
	}
	
	/*-----------------------------------	when click Game Rule button ------------------------- */
	Rule(){
		this.setState({
			message: "game rule"
		},function(){
			$("#MessageBox").modal({
				backdrop: "static"
			});
		});
	}
	
	/*-----------------------------------	when click Pass button ------------------------- */
	Pass(){
		this.setState(function(prevState, props){
			/*------------       change player    ----------------*/
			var newcurrentplayer = !prevState.currentplayer
			/*------------       save new action in game history    ----------------*/
			var oldhistory = prevState.history;
			var newhistory = [];
			if (oldhistory.length === 0)
				newhistory.push([null, null])
			else{
				newhistory = oldhistory.map(function(array) {
    				return array.slice();																		
				});		
				newhistory.push([null, null]);
			}
			return{
				currentplayer: newcurrentplayer,
				history: newhistory
			}
		});
	}
	
	/*-----------------------------------	when click Undo button ------------------------- */
	Undo(){
		this.setState({
			message: "undo"
		},function(){
			$("#MessageBox").modal({
				backdrop: "static"
			});
		});
	}
	
	undoConfirm(){
		var lastAction = this.state.history[this.state.history.length-1];
		var x = lastAction[0];
		var y = lastAction[1];
		if(x===null&&y===null){
			/*---------- It means the last action is a 'Pass' action ----------*/
			this.setState(function(prevState, props){
				/*------------       change player    ----------------*/
				var newcurrentplayer = !prevState.currentplayer
				/*------------       save new action in game history    ----------------*/
				var oldhistory = prevState.history;
				var newhistory = [];
				if (oldhistory.length === 0)
					newhistory.push([null, null])
				else{
					newhistory = oldhistory.map(function(array) {
    					return array.slice();																		
					});		
					newhistory.pop();						//remove last history
				}
				return{
					currentplayer: newcurrentplayer,
					history: newhistory,
					winner: null								 // no matter what action is this, once undo, there shouldn't have winner
				}
			},function(){
				/* Of course, the 'Pass' button can be used again */
				$("#Pass").removeAttr("disabled");
			});
		}
		else{
			/*---------- It means the last action is not a 'Pass' action ----------*/
			this.setState(function(prevState, props){
				/*------------       change player    ----------------*/
				var newcurrentplayer = !prevState.currentplayer
				/*------------       save new action in game history    ----------------*/
				var oldhistory = prevState.history;
				var newhistory = [];
				if (oldhistory.length === 0)
					newhistory.push([null, null])
				else{
					newhistory = oldhistory.map(function(array) {
    					return array.slice();																		
					});		
					newhistory.pop();						//remove last history
				}
				/*------------       modify chessboard to last state    ----------------*/
				var newchessArray = prevState.chessarray.map(function(array) {
    				return array.slice();																		//copy
				});			
				newchessArray[y][x] = null;				//reset to null
				/*------------       clean that cell    ----------------*/
				var row = $("#chessboard").find("tr").eq(y);
				var cell = $(row).children("td").eq(x);
				$(cell).text("");
				return{
					currentplayer: newcurrentplayer,
					history: newhistory,
					chessarray: newchessArray,
					winner: null								 // no matter what action is this, once undo, there shouldn't have winner
				}
			},function(){
				/* Of course, the 'Pass' button can be used again */
				$("#Pass").removeAttr("disabled");
			});
		}
	}
	
	/*-----------------------------------	when click Info button ------------------------- */
	Info(){
		console.log(this.state);
	}
	
	/*-----------------------------------	when click Replay button ------------------------- */
	Replay(){
		$("#ReplayBox").modal({
				backdrop: "static"
		});
		// Adjust CSS style to make sure the cells of table are square
	}

	/*-----------------------------------	when click Reset button ------------------------- */
	Reset(){
		this.setState({
				message: "reset confirm"
		},function(){
			$("#MessageBox").modal({
				backdrop: "static"
			});
		});
	}
	
	resetConfirm(){
		this.setState({
				message: null,
				currentplayer: false, 
				chessarray: squareArray(this.props.number), 
				history: [],
				winner: null
		},function(){
			$("#chessboard").find("td").each(function(){
				$(this).text("");
			})
		});
	}
	
	render(){
		var list = ["Game Rule", "Pass", "Undo", "Replay", "Reset"];
		var player = this.state.currentplayer === true?"O":"X";
		var subtitle = <p className="text-danger text-center font-weight-bold">Next Player: 
									<span className="ml-3">{player}</span>
							 </p>
		if(this.state.winner!==null){
			subtitle = <p className="text-danger text-center font-weight-bold">Winner: 
									<span className="ml-3">{this.state.winner}</span>
							 </p>
		}
		return	<div className="container p-3">
					<div>
						<h3 id="title" className="d-block text-center p-2 border">{this.props.name}</h3>
						{subtitle}
			  		</div>	
					<div className="row">
						<div className="col-7 offset-1">
							<Chessboard 
								number={this.props.number} currentplayer = {this.state.currentplayer}
								nextstep = {(event)=>this.nextstep(event)}
							></Chessboard>
						</div>
						<div className="col-3">
							<ActionPanel list={list} onClick={(event)=>this.onClick(event)}
							></ActionPanel>
						</div>
					</div>
					<div id="ModalContainer">
						<Message messagetype={this.state.message} winner={this.state.winner}
							resetComfirm={(event) => this.resetConfirm(event)}
							undoComfirm={(event) => this.undoConfirm(event)}
						></Message>
						<Replay number={this.props.number} history={this.state.history}></Replay>
					</div>
				</div>
	}
}



ReactDOM.render(<App name="Gomoku Game" number="15"/>, $("#root")[0]);




